.App {
  text-align: center; 
  margin: 0 auto;
  width: 50%;
}

.progressVisualFull {
  display: flex;
  height: 15px;
  margin: 20px 0;
  border-radius: 50px;
  background-color: #EDEDED
}

.progressVisualPart {
  /* Number of the seconds for complete animation */
  transition: width 2s;
}

.progressVisualPart:first-child {
  border-top-left-radius: 50px;
  border-bottom-left-radius: 50px;
}
.progressVisualPart:last-child {
  border-top-right-radius: 50px;
  border-bottom-right-radius: 50px;
}
